
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'AsideBar',

  data () {
    return {
      darkTheme: false
    }
  },

  methods: {
    handleChangeTheme ()  {
      this.darkTheme = !this.darkTheme;
      this.$emit('onChangeTheme', this.darkTheme);
    }
  }
});
