
import { defineComponent } from "vue";

export default defineComponent({
  name: "Stopwatch",

  props: {
    secondsTime: {
      default: 0,
      required: true,
      type: Number
    },

    color: {
      default: '',
      required: false,
      type: String
    }
  },

  computed: {
    elapsedTime (): string {
      return new Date(this.secondsTime * 1000).toISOString().substr(11, 8);
    }
  }
});
